/* Rectangle 4142 */
.box {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 190px;
  left: 10vw;
  top: -50px;
  padding-top: 50px;
  background: #1A1A1A;
  z-index: 500;
}

.shadowBox {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 190px;
  left: -8px;
  top: -8px;
  padding-top: 50px;
  background: #525252;
  z-index: 400;
}



/* ABOUT BEAR */
.title {
  position: relative;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 50px;
  /* identical to box height, or 122% */
  display: flex;
  align-items: center;
  text-align: center;
  left: 30px;
  letter-spacing: -0.05em;
  
  color: #AAAAAA;
}

.titleSpan {
  font-weight: 700;
}




/* Line 1 */
.bar {
  position: relative;
  width: 300px;
  height: 0px;
  left: 80px;
  top: 5px;
  border: 2px solid #FFFFFF;
}

