.publication {
  background-color: #3a3a3a;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  justify-content: center;
  padding: 100px 100px;
}

@media (max-width: 991px) {
  .publication {
    padding: 100px 20px;
  }
}

.article {
  display: flex;
  width: 100%;
  flex-direction: column;
  padding: 92px 0 23px;
} 

@media (max-width: 991px) {
  .article {
    max-width: 100%;
  }
}

.SectionSelect {
  position: fixed;
  display: flex;
  right: 50px;
  width: min-content;
  top: 140px;
}

.searchBox {
  border-radius: 5px;
  background-color: #505050;
  align-self: flex-end;
  margin-right: 50px;
  width: 209px;
  max-width: 100%;
  color: #fff;
  letter-spacing: -0.18px;
  padding: 14px 13px;
  font: 500 15px/1 Poppins, sans-serif;
  border: 1px solid #747474;
}

@media (max-width: 991px) {
  .searchBox {
    margin-right: 10px;
    padding-right: 20px;
  }
}

.publications {
  display: flex;
  margin-top: 22px;
  flex-direction: column;
  padding: 0 32px;
}

@media (max-width: 991px) {
  .publications {
    max-width: 100%;
    padding: 0 20px;
  }
}

.dividerLine {
  width: 174px;
  height: 5px;
  border: 5px solid #fff;
  margin-bottom: 10px
}

.publicationItem {
  border-color: #ddd;
  border-bottom-width: 1px;
  display: flex;
  width: 100%;
  flex-direction: column;
  margin: 4px 0 0 18px;
  padding: 21px 35px 21px 0;
}

@media (max-width: 991px) {
  .publicationItem {
    max-width: 100%;
    padding-right: 20px;
  }
}

.publicationTitle {
  color: #fff;
  align-self: flex-start;
  font: 800 21px/35px Poppins, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 991px) {
  .publicationTitle {
    max-width: 100%;
  }
}

.publicationDetails {
  display: flex;
  margin-top: 5px;
  width: 100%;
  align-items: flex-start;
  gap: 0 100px;
  flex-wrap: wrap;
}

@media (max-width: 991px) {
  .publicationDetails {
    max-width: 100%;
  }
}

.authorInfo {
  color: #dadada;
  letter-spacing: -0.18px;
  align-self: flex-start;
  flex-grow: 1;
  width: 715px;
  font: 600 17px/30px Pretendard, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 991px) {
  .authorInfo {
    max-width: 100%;
  }
}

.actionButtons {
  align-self: flex-end;
  display: flex;
  margin-top: 7px;
  align-items: flex-start;
  gap: 13px;
  color: #424242;
  letter-spacing: -0.6px;
  font: 500 15px Noto Sans KR, -apple-system, Roboto, Helvetica, sans-serif;
}

.actionButtonsWrapper {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.actionButton {
  border-radius: 17px;
  background-color: #d4d4d4;
  display: flex;
  height: 38px;
  align-items: center;
  overflow: hidden;
  line-height: 35px;
  justify-content: flex-start;
  padding: 0 6px;
}

.actionButtonInner {
  display: flex;
  height: 38px;
  align-items: center;
  overflow: hidden;
  line-height: 35px;
  justify-content: flex-start;
  padding: 0 6px;
}
.verticalDivider {
  width: 1px;
  height: 20px;
  background-color: #424242;
}

.actionIcon {
  aspect-ratio: 1;
  object-fit: contain;
  object-position: center;
  width: 22px;
  align-self: stretch;
  margin: auto 0;
}

.actionText {
  align-self: stretch;
  margin: auto 0;
  font-weight: 550;
  color: #424242;
}

@media (max-width: 991px) {
  .actionButtonInner .actionText {
    max-width: 100px !important;
    margin-left: 5px;
    opacity: 1 !important;
    
  }
  .actionButton {
    margin-top: 15px;
  } 
}



.actionButtonInner .actionText {
  /*visibility: hidden;  기본적으로 span을 숨김 */
  opacity: 0;
  max-width: 0; /* 처음에는 너비가 0 */
  white-space: nowrap; /* 텍스트가 줄 바꿈되지 않도록 */
  overflow: hidden;
  
  transition: max-width 0.5s ease, opacity 0.5s ease; /* 확장되는 애니메이션 */
}

.actionButtonInner:hover .actionText {
  /*visibility: visible; /* 마우스를 올리면 span을 보이게 함 */
  opacity: 1;
  margin-left: 5px;
  max-width: 100px; /* 원하는 너비로 확장 */
}



.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.lightDividerLine {
  width: 100%;
  border: 1px solid #ddd;
  margin-top: 20px
}