.gradientPicker {
  display: flex;
  align-items: center;
  height: 40px;
}

.colorInput {
  width: 30px;
  height: 30px;
  border-radius: 10%;
  border: none;
  cursor: pointer;
}

.gradientBar {
  flex-grow: 1; /* 막대가 가능한 공간을 다 차지하도록 설정 */
  height: 20px; /* 막대의 높이 */
  border-radius: 5px; /* 둥근 모서리 */
  display: flex; /* 자식 요소를 가로로 배치 */;
  align-items: center; /* 자식 요소를 세로로 가운데 정렬 */
  justify-content: space-between;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* 그림자 효과 */
}
