/* Landing.module.css */

/* 히어로 섹션 */
.hero {
  background: linear-gradient(122.01deg, #ECECEC 0%, #B4B4B4 100%), linear-gradient(122.01deg, #3A3A3A 0%, #2B2B2B 100%);

  color: white;
  text-align: center;
  padding: 100px 20px;
  padding-top: 160px;
  display: flex;
  align-items: flex-start;
  justify-content: end;
  flex-direction: column;
  min-height: calc(100vh);
  position: relative;
}


.heroContent {
  left: 100px;
  max-width: 800px;
  margin-left: 10vw;
  margin-bottom: 20vh;
  animation: fadeIn 2s ease-in-out;
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 900;
  text-align: start;

  align-items: start;
}


.spliterLine {
  width: 141px;
  height: 0px;
  margin: 20px 0 20px 10px;
  border: 3px solid #696969;
  border-radius: 3px;
}

.heroTitleText {
  text-align: left;
  font-size: 82px;
  color: #555454;
  line-height: 78px;
}

.heroTitle {
  font-size: 85px;
  background: linear-gradient(92.61deg, #7596AE -12.61%, #63588F 78.49%);
  margin-bottom: 20px;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}


.heroText {
  margin-left: 5px;
  line-height: 40px;
  font-weight: 350;
  font-size: 36px;
  color: #646464;

}
.heroTextHighlight {
  color: #768BD2;
  font-weight: 700;
}

.heroButton {
  background-color: #f0e6f6;
  color: #4A90E2;
  font-size: 18px;
  font-weight: bold;
  padding: 12px 30px;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.heroButton:hover {
  background-color: #d1b3e0;
  color: white;
  transform: translateY(-3px);
}

.heroButton:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(209, 179, 224, 0.5);
}

.triangleContainer {
  margin-top: 40px; /* 삼각형 컴포넌트와 다른 요소 사이의 간격 조정 */
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}


.research-areas {
  background-color: #f4f4f4;
  padding: 60px 20px;
  text-align: center;
}

.introduction h1 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
  margin: 50px 20px 40px 20px;
  font-family: Rockwell;
}

.introduction p {
  margin: 10px;
  text-indent: 1em;
}

.research-areas h1 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
  margin: 50px 20px 40px 20px;
  font-family: Rockwell;
}

.cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  padding: 20px;
  width: 30%;
  margin: 20px 0;
  transition: transform 0.3s;
}

.card:hover {
  transform: translateY(-10px);
}

.card h3 {
  margin: 0 0 10px 0;
  font-family: 'Poppins', sans-serif;
  color: #4A90E2;
}

.card p {
  margin: 0 0 20px 0;
  color: #666;
}

.card a {
  color: #9B59B6;
  text-decoration: none;
  font-weight: bold;
}

.card a:hover {
  text-decoration: underline;
}