
.PeopleSection{
  display: flex;
  width: 100%;
  flex-direction: column;
  padding: 0 49px 77px;
}

@media (max-width: 740px) {
  .PeopleSection{
    max-width: 100%;
    padding: 0 20px;
  }
}

.sectionHeader {
  border-color: #a9a9a9;
  border-bottom: 1px solid;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #d3d3d3;
  white-space: nowrap;
  padding: 0 80px 64px 0;
  font: 700 50px/1.2 Roboto, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 740px) {
  .sectionHeader {
    max-width: 100%;
    padding-right: 20px;
    font-size: 40px;
    white-space: initial;
  }
}

.headerBackground {
  background-color: #d2d2d2;
  width: 50px;
  height: 3px;
}

.headerTitle {
  margin-top: 29px;
}

@media (max-width: 740px) {
  .headerTitle {
    font-size: 40px;
  }
}

.professorCard,
.memberCard {
  display: flex;
  gap: 20px;
  margin-top: 65px;
  width: 100%;
  max-width: 1189px;
}

@media (max-width: 740px) {
  .professorCard,
  .memberCard {
    flex-direction: column;

    align-items: center;
    gap: 40px;
    margin-top: 40px;
  }

  .professorIcon {
    display: none;
  }
  .professorName {
    justify-content: center;
    font-size: 3.8em !important;

  }
  .professorDescription {
    font-size: 1.1em !important;
    text-align: center;
    line-height: 1.7em !important;
    padding-left: 0px !important;
  }
}

.professorImageWrapper,
.memberImageWrapper {
  flex: 0 0 35%;
  border: 1px solid #dbdbdb;
  padding: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 18em;
  max-width: calc(18em*0.8);
}



@media (max-width: 740px) {
  .professorImageWrapper,
  .memberImageWrapper {
    width: 70%;
    max-height: 100%;
    max-width: 100%;
  }
}

.professorImage,
.memberImage {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.professorInfo,
.memberInfo {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.professorName,
.memberName {
  display: flex;
  align-items: center;
  gap: 9px;
  color: #d3d3d3;
  font: 700 40px/1 Roboto, sans-serif;
}

.professorIcon,
.memberIcon {
  width: 16px;
  height: auto;
}

.professorDescription,
.memberDescription {
  margin-top: 8px;
  padding-left: 10px;
  color: #cecece;
  font: 350 24px/35px 'Noto Sans KR', sans-serif;
  letter-spacing: -0.6px;
}

.professorContact,
.memberContact {
  display: flex;
  margin-top: 12px;
  align-items: center;
  gap: 15px;
  padding: 0 10px;
}

@media (max-width: 740px) {
  .professorContact,
  .memberContact {
    justify-content: space-between;
    gap: auto;
  }
}

.professorEmail,
.memberEmail,
.memberGithub {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  color: #cecece;
  font-weight: 900;
}

.emailIcon,
.githubIcon {
  width: 21px;
  height: auto;
}

.emailAddress,
.githubUrl {
  font-weight: 350;
}

.cvButton {
  border-radius: 8px;
  background-color: #1d1d1d;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 8px;
  font-size: 15px;
  color: #bbbbbb;
  font-weight: 500;
  cursor: pointer;
  border: none;
}

.cvIcon {
  width: 22px;
  height: auto;
}

.memberContact {
  justify-content: space-between;
}

.memberGithub::after {
  content: '';
  display: inline-block;
  width: 1px;
  height: 20px;
  background-color: #a9a9a9;
  margin: 0 15px;
}