.footer {
  background-color: #333;
  color: #fff;
  padding: 20px 0;
  text-align: center;
}

.footerContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
  flex-wrap: wrap;
}

.address, .contact {
  flex: 1;
  margin: 10px;
  text-align: left;
}

.footerTitle {
  font-size: 1.2rem;
  margin-bottom: 5px;
}

.footerText {
  margin: 0;
  font-size: 1rem;
  text-decoration: none;
}

.footerText:hover {
  text-decoration: underline;
}
