.hero {
  background: linear-gradient(135deg, #ffdfdf, #9B59B6);
  color: white;
  text-align: center;
  padding: 100px 20px;
  padding-top: 160px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 60vh;
  position: relative;
}

.heroContent {
  max-width: 800px;
  margin: 0 auto;
  animation: fadeIn 2s ease-in-out;
}

.heroTitle {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.heroText {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.imageSlider {
  width: 100%;
  height: 400px;
  margin: 40px 0;
  overflow: hidden;
}

.container {
  max-width: 800px;
  margin: 40px auto;
  padding: 0 20px;
}

.section {
  margin-bottom: 40px;
}

.sectionHeader {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.icon {
  font-size: 2rem;
  margin-right: 10px;
  color: #4A90E2;
}

.sectionTitle {
  font-size: 1.8rem;
  color: #4A90E2;
}

.sectionText {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 10px;
}

.researchList {
  list-style: none;
  padding-left: 0;
  margin-top: 10px;
}

.researchList li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.listIcon {
  font-size: 1.5rem;
  color: #4A90E2;
  margin-right: 10px;
}

.SliderWrapper {
  width: 100%;
  height: 400px;
  padding: 20px;
  overflow: hidden;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
.mainContent {
  padding: 40px;
}

.aboutSection, .researchSection, .contactSection {
  margin-bottom: 40px;
}

.sectionTitle {
  font-size: 2em;
  color: #004080;
  margin-bottom: 20px;
}

.sectionText {
  font-size: 1.1em;
  line-height: 1.6;
  color: #333;
}

.card {
  height: 100%;
}

.cardTitle {
  font-size: 1.2em;
  color: #004080;
}

.cardText {
  font-size: 1em;
  color: #333;
}

.contactItem {
  display: flex;
  align-items: center;
}

.contactIcon {
  margin-right: 8px;
}

.contactText {
  font-size: 1.1em;
  color: #333;
}
