.slider {
  max-width: 1000px;
  margin: 0 auto;
}

.imageContainer {
  position: relative;
  width: 100%;
  height: 320px;
  overflow: hidden;
  border-radius: 10px;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.placeholder {
  background-color: #cccccc; /* 회색 배경 */
  width: 100%;
  height: 100%;
  display: none; /* 기본적으로 보이지 않음 */
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
}

.imageContainer img[style*="display: none"] ~ .placeholder {
  display: flex;
}
