@keyframes slideIn {
  from {
    
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.animateIn {
  animation: slideIn 0.5s both;
}

.animateOut {
  animation: slideOut 0.5s both;
}

.sidebar {
  
  font-size: 1.5em;
  overflow: hidden;
  background: var(--127001-linear-havelock-blue-wisteria, linear-gradient(144deg, var(--headerGradientFirst) 0%, var(--headerGradientSecond) 100%));
  position: fixed;
  top: 0px;
  padding-top: 50px;
  right: 0;
  height: 100vh;
  width: 250px;
  transform: translateX(105%);
  transition: transform 0.7s ease, box-shadow 0.7s 0.2s ease; /* 박스 그림자 추가 */
  z-index: 1000;
  box-shadow: none;
  border-left: 1px solid rgba(0, 0, 0, 0.1); /* 오른쪽 경계선 추가 */
}

.sidebarScreen {
  position: fixed; /* 화면에 고정된 위치 */
  top: 0; /* 화면의 상단 */
  left: 0; /* 화면의 왼쪽 */
  width: 100%; /* 화면 전체 너비 */
  height: 100%; /* 화면 전체 높이 */
  background: rgba(0, 0, 0, 0.5); /* 반투명 배경 */
  z-index: 999; /* 가장 위에 표시되도록 */
  display: none; /* 기본적으로 숨김 */
  overflow: hidden; /* 스크롤바가 나타나지 않도록 */
}


.sidebarScreen.open {
  display: block;
}

.sidebar.open {
  transform: translateX(0);
  box-shadow:-2px 5px 10px rgba(0,0,0,0.3);
}

.sidebarContent {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;
}

.navLinks {
  display: flex;
  flex-direction: column;
  gap: 0.7em;
  color: var(--navbar-font-color, white);
  font-weight: 700;
  line-height: 1.7rem;
}

.navLink {
  opacity: 0;
  white-space: nowrap;
  font-family: Poppins, -apple-system, Roboto, Helvetica, sans-serif;
  font-weight: 600;
  text-decoration: none;
  position: relative;
  color: var(--navbar-font-color, white);
  transition: color 0.3s, background-color 0.3s;
  margin: 10px;
  border-radius: 5px; /* 둥근 모서리 추가 */
  transition: all 0.3s ease-in-out;
}

.navLink::before {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -2px;
  left: 0;  
  background-color: white;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}

.navLink:hover::before {
  visibility: visible;
  width: 100%;
}

.navLink:hover {
  color: #ececec;
  margin-left: 15px;
}

