/* Global Styles */
/* 기본 설정 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

:root {
  --HeaderGradientFirst: #CFC6E8;
  --HeaderGradientSecond: #1A1A2E;
  --HeaderGradientAngle: 90deg;
  --gradientFirst: #ECECEC;
  --gradientSecond: #B4B4B4;
  --HeaderlogoHighlight: #FFD700;
  --Headercolor: #e3e3e3;
}


/* font import */
@import './fonts.css';
